import myScrollX from '@/components/my-scrollX/my-scrollX.vue'
import TopInput from '@/components/top-input/top-input.vue'
import MyList from '@/components/my-list/my-list.vue'
export default {
	components: {
		myScrollX,
		TopInput,
		MyList,
	},
	data() {
		return {
			showClassify: false,
			scrollTopList:[{
				id:0,
				name: '无分类',
			},{
				id:1,
				name: '英语',
			},{
				id:2,
				name: '记忆',
			},{
				id:3,
				name: '医疗',
			},{
				id:4,
				name: '法律',
			},{
				id:3,
				name: '少儿',
			},],
			currentTopTab: 0,
			beforeColor: '#666666',
			afterColor: '#2E3B67',
			placeholderText: '请输入搜索内容',
			changeValue: '',
			trainInfo:{
				collectsList:[
					{
						collectsPic:'https://img1.baidu.com/it/u=1091405991,859863778&fm=26&fmt=auto&gp=0.jpg',
						collectsName:'1111',
						collectsAuthor: '111',
						attentionNum: '11',
						collectsRemarks: '111111111111111',
						studyDate: '11',
						studyMonth: '11',
						vipType: 1,
						id:1,
						attentionType: 1
					},
					{
						collectsPic:'https://img1.baidu.com/it/u=1091405991,859863778&fm=26&fmt=auto&gp=0.jpg',
						collectsName:'1111',
						collectsAuthor: '111',
						attentionNum: '11',
						collectsRemarks: '111111111111111',
						studyDate: '11',
						studyMonth: '11',
						vipType: 1,
						id:1,
						attentionType: 0
					},
				]
			},
			page:1,
			contentText: {
				contentdown: '查看更多',
				contentrefresh: '加载中',
				contentnomore: '- 暂时没有新内容了呢 -'
			},
			status: 'noMore',
		};
	},
	onPullDownRefresh() {
		this.page = 1;
		this.trainInfo = {};
		uni.showLoading({
			title: '加载中'
		});
		this.getTrainList()
		uni.hideLoading();
	},
	// onReachBottom(){ //不知道一共多少条
	// 	if (this.page * 10 < this.total) {
	// 		this.page = this.page + 1;
	// 		this.getTrainList();
	// 	}
	// },
	mounted(){
		this.getTrainList()
	},
	methods:{
		// 获取首页数据列表
		getTrainList(){
			this.$Request.get(`/appCollectsController.do?getTrainList&page=${this.page}&type=1&groupId`).then(res => {
				if(res.code == 0){
					this.trainInfo = res.data
					this.trainInfo.collectsList =  [...this.trainInfo.collectsList, ...res.data.collectsList]
				}else if(res.code == '-118'){
					this.status = 'noMore'
				}else{
					uni.showToast({
						title: res.info,
						icon: 'none'
					})
				}
			})
		},
		gotoListDetail(item){
			console.log('1',item)
			uni.navigateTo({
				url: '/pages/train/imageMemory/numEleEntry'
			})
		},
		// 收藏
		clickAttention(item, index){
			if(uni.getStorageSync('userInfo')){
				let memberId = JSON.parse(uni.getStorageSync('userInfo')).id
				let collectsId = item.id
				this.$Request.get(`/appAttentionController.do?takeCollectsAttention&memberId=${memberId}&collectsId=${collectsId}`)
				.then(res => {
					if(res.code == 0){
						this.trainInfo.collectsList[index].attentionType = item.attentionType == 1 ? 0 : 1
					}else{
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					}
				})
			}else{
				uni.showToast({
					title: '您尚未登录，正在跳往登录页面。。。',
					icon: 'none'
				})
				setTimeout(() => {
					uni.navigateTo({
						url:'/pages/loginAll/login'
					})
				}, 1000)
			}
		},
		gotoUrl(url){
			uni.navigateTo({
				url: url
			})
		},
		tabChange(item, index){
			this.currentTopTab = index
			console.log(item, index)
		},
		changeInput(value){
			console.log(value)
			this.changeValue = value
		},
	}
}
